import React from 'react';
import './HeaderSlider.css';
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Autoplay, Pagination } from 'swiper';
import 'swiper/css';
import "swiper/css/pagination";

import img3 from '../../../assets/img/img3.png';

SwiperCore.use([Autoplay, Pagination]);


const HeaderSlider = () => {
    return (
        <div id='headerSlider'>
            <Swiper grabCursor={true} autoplay={{delay: 5000, disableOnInteraction: false}} pagination={{type: "progressbar"}}>
                <SwiperSlide>
                    <div className="wrap">
                        <img src="https://gunaybank.com/public/sliders/1640254755/mida-xetti-ile-guzestli-ipoteka_1640254755.png" alt="img1" />
                        <div>
                            <strong>Azrbaycan Respublikasnn Mnzil naat Dvlt Agentliyi</strong>
                            <p>Artq mnzil arzusunda olan hr ks mnzil almaq ans ld edck.</p>
                            <button>Daha trafl</button>
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide>
                    <div className="wrap">
                        <img src="https://gunaybank.com/public/sliders/1624548729/odenis-kartlari_1624548729.png" alt="img2" />
                        <div>
                            <strong>dni kartlar</strong>
                            <p>Gndlik al-veri v dnyann hr yerind rahat v thlksiz istifad ed bilrsiniz</p>
                            <button>Visa kartlar</button>
                            <button>Master kartlar</button>
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide>
                    <div className="wrap">
                        <img src={img3} alt="img3" />
                        <div>
                            <strong>poteka kreditlri</strong>
                            <p>Mnzil probleminin srfli yolu Gnaybank ASC-dn poteka krediti ld etmkdir!</p>
                            <button>Daha trafl</button>
                        </div>
                    </div>
                </SwiperSlide>
            </Swiper>
        </div>
    );
};

export default HeaderSlider;